#choosePlayer > div {
    text-align: center;
    height: 100vh;
    display: block;
    position: absolute;
    top: 0px;
}

#cha {
    width: 40vw;
    /* background: rgba(255,0,0,0.5); */
}

#cha .character {
    left: 50% !important;
    top: 50%;
    transform: translate(-50%,-50%) !important;
}

#details{
    width: 60vw;
    right: 0px;
    /* background: rgba(0,0,0,0.5); */
}

#details > div{
    position: absolute;
    display: block;
    width: 100%;
    top: 50%;
    transform: translate(0,-50%);
}


#choosePlayer input[type="text"]{
    color: #fff;
    background: transparent;
    border: 0;
    position: relative;
    display: inline-block;
    width: 150px;
    height: 35px;
    font-size: 30px;
    outline: none;
    left: 10px;
    margin:0;
    font-family: 'VT323', monospace !important;
    margin-right: 15px;
    padding: 0;
    z-index: 2;
}

#namoagain {
	position: relative;
    padding: 5px 10px;
    height: 39px;
    display: inline-block;
    margin-bottom: 20px;
    background: #92CD41;
	box-shadow: inset -4px -4px 0px 0px #4AA52E;
}

#namoagain.fuckd {
	background: #CE372B;
	box-shadow: inset -4px -4px 0px 0px #8C2022;
}

#namoagain:before, #namoagain:after {
	content: '';
	position: absolute;
	width: 100%;
	height: 100%;
	box-sizing: content-box;
}

#namoagain:before {
	top: -4px;
	left: 0;
	border-top: 4px black solid;
	border-bottom: 4px black solid;
}

#namoagain:after {
	left: -4px;
	top: 0;
	border-left: 4px black solid;
	border-right: 4px black solid;
}


#choosePlayer button {
    color: #fff;
    /* border: 4px solid #a78d19; */
    /* background: #e6c11d; */
    display: inline-block;
    margin: 20px;
    padding: 7px 14px;
    font-size: 30px;
    border: 0;
    outline: none;
    font-family: 'VT323', monospace !important;
    cursor: pointer;
}


.eightbit-btn {
	background: #2a84d8;;
	display: inline-block;
	position: relative;
	text-align: center;
	font-size: 30px;
	font-family: 'Press Start 2P', cursive;
	text-decoration: none;
	color: white;
	box-shadow: inset -4px -4px 0px 0px #1d5d99;
}

.eightbit-btn:hover {
	background: #277ccc;
	box-shadow: inset -6px -6px 0px 0px #1d5d99;
}

.eightbit-btn:active {
	box-shadow: inset 4px 4px 0px 0px #1d5d99;
}

.eightbit-btn:before, .eightbit-btn:after {
	content: '';
	position: absolute;
	width: 100%;
	height: 100%;
	box-sizing: content-box;
}

.eightbit-btn:before {
	top: -4px;
	left: 0;
	border-top: 4px black solid;
	border-bottom: 4px black solid;
}

.eightbit-btn:after {
	left: -4px;
	top: 0;
	border-left: 4px black solid;
	border-right: 4px black solid;
}

.eightbit-btn--proceed {
	background: #F7D51D;
	box-shadow: inset -4px -4px 0px 0px #E59400;
}

.eightbit-btn--proceed:hover {
	background: #F2C409;
	box-shadow: inset -6px -6px 0px 0px #E59400;
}

.eightbit-btn--proceed:active {
	box-shadow: inset 4px 4px 0px 0px #E59400;
}

hr {
	margin: 10px auto;
	max-width: 100px;
	display: block;
	height: 1px;
	border: 0;
	border-top: 2px solid #000;
	padding: 0;
}



